
function appendAll(...args) {
  for (let i = 0; i < args.length; i += 1) {
    document.body.appendChild(args[i]);
  }
}
function unwrap(wrapper) {
  // place childNodes in document fragment
  const docFrag = document.createDocumentFragment();
  while (wrapper.firstChild) {
    const child = wrapper.removeChild(wrapper.firstChild);
    docFrag.appendChild(child);
  }

  // replace wrapper with document fragment
  wrapper.parentNode.replaceChild(docFrag, wrapper);
}
export default class AppView {
  constructor(titles, page) {
    this.titles = titles;
    this.page = page;
  }

  renderNext() {
    const clips = document.createElement('ul');
    clips.setAttribute('class', 'one');
    clips.innerHTML = this.titles.clipsData.getClipNames.map(titles => `
    <li>
      <div class='clip'>
        <img class='preview${this.page} preview'>
        <div class='clip-title${this.page} clip-title'>
          <a class='clip-link${this.page} clip-link'>${titles}</a>
          </div>
            <div class='clip-info'>
              <div class='clip-icon clip-channel-title${this.page} clip-channel-title'></div>
              <div class='clip-date${this.page} clip-date'></div>
              <div class='clip-view-count${this.page} clip-view-count'></div>
              </div>
              <div class='clip-description${this.page} clip-description'>
              </div>
            </div>
        </div>
      </div>
    </li>`).join('');
    document.getElementById('list').appendChild(clips);
    const links = document.getElementsByClassName(`clip-link${this.page}`);
    for (let i = 0; i < links.length; i += 1) {
      links[i].setAttribute('href', `https://www.youtube.com/watch?v=${this.titles.clipsData.getClipLink[i]}`);
      links[i].setAttribute('target', '_blank');
    }

    // render clip image
    const previews = document.getElementsByClassName(`preview${this.page}`);
    for (let i = 0; i < previews.length; i += 1) {
      previews[i].setAttribute('src', `${this.titles.clipsData.getClipImage[i]}`);
    }

    // render Author channel
    const clipBox = document.getElementsByClassName(`clip-channel-title${this.page}`);
    for (let i = 0; i < clipBox.length; i += 1) {
      clipBox[i].innerHTML = this.titles.clipsData.getClipChannelTitle[i];
    }

    // render data publish
    const dates = document.getElementsByClassName(`clip-date${this.page}`);
    const data = [];
    for (let i = 0; i < dates.length; i += 1) {
      data[i] = new Date(this.titles.clipsData.getClipDate[i]);
      dates[i].innerHTML = `${data[i].getFullYear()}-${((`0${data[i].getMonth() + 1}`)).slice(-2)}-${(`0${data[i].getUTCDate()}`).slice(-2)}`;
    }

    // render clip description
    const desc = document.getElementsByClassName(`clip-description${this.page}`);
    for (let i = 0; i < desc.length; i += 1) {
      desc[i].innerHTML = this.titles.clipsData.getClipDescription[i];
    }

    // render clip views
    const views = document.getElementsByClassName(`clip-view-count${this.page}`);
    for (let i = 0; i < views.length; i += 1) {
      views[i].innerHTML = this.titles.clipViews[i];
    }
    unwrap(document.querySelector('.one'));
  }

  render() {
    const main = document.querySelector('.wrapper');
    const wrapper = document.createElement('div');
    wrapper.setAttribute('class', 'clips');
    const content = document.createElement('ul');
    content.setAttribute('class', 'clip-list');
    content.setAttribute('id', 'list');
    content.innerHTML = this.titles.clipsData.getClipNames.map(titles => `
    <li>
      <div class='clip'>
        <img class='preview'>
        <div class='clip-title'>
          <a class='clip-link'>${titles}</a>
          </div>
            <div class='clip-info'>
              <div class='clip-icon clip-channel-title'></div>
              <div class='clip-date'></div>
              <div class='clip-view-count'></div>
              </div>
              <div class='clip-description'>
              </div>
            </div>
        </div>
      </div>
    </li>`).join('');
    wrapper.appendChild(content);
    main.appendChild(wrapper);

    // render interactive link to video
    const links = document.getElementsByClassName('clip-link');
    for (let i = 0; i < links.length; i += 1) {
      links[i].setAttribute('href', `https://www.youtube.com/watch?v=${this.titles.clipsData.getClipLink[i]}`);
      links[i].setAttribute('target', '_blank');
    }

    // render clip image
    const previews = document.getElementsByClassName('preview');
    for (let i = 0; i < previews.length; i += 1) {
      previews[i].setAttribute('src', `${this.titles.clipsData.getClipImage[i]}`);
    }

    // render Author channel
    const clipBox = document.getElementsByClassName('clip-channel-title');
    for (let i = 0; i < clipBox.length; i += 1) {
      clipBox[i].innerHTML = this.titles.clipsData.getClipChannelTitle[i];
    }

    // render data publish
    const dates = document.getElementsByClassName('clip-date');
    const data = [];
    for (let i = 0; i < dates.length; i += 1) {
      data[i] = new Date(this.titles.clipsData.getClipDate[i]);
      dates[i].innerHTML = `${data[i].getFullYear()}-${((`0${data[i].getMonth() + 1}`)).slice(-2)}-${(`0${data[i].getUTCDate()}`).slice(-2)}`;
    }

    // render clip description
    const desc = document.getElementsByClassName('clip-description');
    for (let i = 0; i < desc.length; i += 1) {
      desc[i].innerHTML = this.titles.clipsData.getClipDescription[i];
    }

    // render clip views
    const views = document.getElementsByClassName('clip-view-count');
    for (let i = 0; i < views.length; i += 1) {
      views[i].innerHTML = this.titles.clipViews[i];
    }
  }


  static createBody() {
    // just hint
    const hint = document.createElement('div');
    hint.setAttribute('class', 'hint');
    hint.innerHTML = '⟵ Input text and press Enter to find videos';

    // div for input elements
    const searchBox = document.createElement('div');
    searchBox.setAttribute('class', 'search-input-box');

    // laber for input search request
    const searchLabel = document.createElement('label');
    searchLabel.setAttribute('class', 'search-input-label');
    searchLabel.setAttribute('for', 'search-input');

    // also as above
    const searchInput = document.createElement('input');
    searchInput.setAttribute('class', 'search-input');
    searchInput.setAttribute('id', 'search-input');
    searchInput.setAttribute('type', 'text');
    searchInput.setAttribute('placeholder', 'What kind of video do you want to find traveller?');
    searchInput.setAttribute('spellcheck', 'false');

    const wrapper = document.createElement('div');
    wrapper.setAttribute('class', 'wrapper');
    wrapper.appendChild(searchBox);
    searchBox.appendChild(searchLabel);
    searchLabel.appendChild(searchInput);
    appendAll(wrapper, hint);
  }

  static createNavigator() {
    const wrapper = document.querySelector('.wrapper');
    const navigator = document.createElement('div');
    navigator.setAttribute('class', 'navigator');
    const buttonNext = document.createElement('button');
    const buttonPrev = document.createElement('button');
    buttonNext.setAttribute('class', 'button next');
    buttonPrev.setAttribute('class', 'button prev');
    buttonNext.innerHTML = 1;
    buttonPrev.innerHTML = 0;
    navigator.appendChild(buttonPrev);
    navigator.appendChild(buttonNext);
    wrapper.appendChild(navigator);
  }
}
