
import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

const key = 'AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y';
let searchRequest;
let limit = 0;
const ids = [];
let page = 0;
let currentPage = 1;
let state;
let clicks = 0;
export default class App {
  constructor() {
    state = {
      url: `https://www.googleapis.com/youtube/v3/search?key=${key}&type=video&part=snippet&maxResults=15&q=${searchRequest}`,
    };
    this.stats = {
      url: `https://www.googleapis.com/youtube/v3/videos?key=${key}&id=${ids}&part=snippet,statistics`,
    };
  }

  static prepare() {

  }

  async start() {
    // for refresh videos
    if (document.querySelector('.clips')) {
      await document.querySelector('.clips').remove();
    }
    if (document.querySelector('.navigator')) {
      await document.querySelector('.navigator').remove();
    }
    const model = new AppModel(state);

    const data = {
      clipsData: await model.getClipInfo(),
    };

    const view = new AppView(data);
    for (let i = 0; i < data.clipsData.getClipLink.length; i += 1) {
      ids[i] = data.clipsData.getClipLink[i];
    }
    this.stats = {
      url: `https://www.googleapis.com/youtube/v3/videos?key=${key}&id=${ids}&part=snippet,statistics`,
    };

    const modelStats = new AppModel(this.stats);
    data.clipViews = await modelStats.getClipVieWCount();

    if (limit === 0) {
      if (document.querySelector('.wrapper')) {
        document.querySelector('.wrapper').remove();
      }
      AppView.createBody();
    } else {
      view.render();
      App.scrollItems();
      setTimeout(() => {
        const ul = document.querySelector('.clip-list');
        ul.style.display = 'flex';
      }, 500);
      AppView.createNavigator();
      App.switchPages();
    }

    const request = document.getElementById('search-input');
    // change link request
    request.addEventListener('input', () => {
      searchRequest = request.value;
      state = {
        url: `https://www.googleapis.com/youtube/v3/search?key=${key}&type=video&part=snippet&maxResults=15&q=${searchRequest}`,
      };
      limit = 1;
    });
    page = 0;
    document.querySelector('.prev').innerHTML = 0;
    document.querySelector('.next').innerHTML = 1;
  }

  // render videos on next page
  static async renderNextPage(token) {
    page += 1;
    state = {
      url: `https://www.googleapis.com/youtube/v3/search?key=${key}&type=video&part=snippet&maxResults=15&q=${searchRequest}&pageToken=${token}`,
    };
    const model = new AppModel(state);
    const data = {
      clipsData: await model.getClipInfo(),
    };
    const view = new AppView(data, page);
    for (let i = 0; i < data.clipsData.getClipLink.length; i += 1) {
      ids[i] = data.clipsData.getClipLink[i];
    }
    this.stats = {
      url: `https://www.googleapis.com/youtube/v3/videos?key=${key}&id=${ids}&part=snippet,statistics`,
    };

    const modelStats = new AppModel(this.stats);
    data.clipViews = await modelStats.getClipVieWCount();
    view.renderNext();
  }

  // interactive slider
  static scrollItems() {
    const slider = document.querySelector('.clip-list');
    let isDown = false;
    let startX;
    let scrollLeft;
    let walk;
    slider.addEventListener('mousedown', (e) => {
      isDown = true;
      slider.classList.add('active');
      ({ scrollLeft } = slider);
      startX = e.pageX - slider.offsetLeft;
    });

    slider.addEventListener('mouseleave', () => {
      isDown = false;
      slider.classList.remove('active');
    });

    slider.addEventListener('mouseup', () => {
      isDown = false;
      slider.classList.remove('active');
    });

    slider.addEventListener('mousemove', (e) => {
      if (!isDown) return;
      e.preventDefault();
      const x = e.pageX - slider.offsetLeft;
      walk = x - startX;
      slider.scrollLeft = scrollLeft - walk;
    });
  }

  // for interactive button-navigator
  static switchPages() {
    const width = 395;
    const count = 4;

    const wrapper = document.querySelector('.wrapper');
    let list = wrapper.querySelector('ul');
    let listElems = wrapper.querySelectorAll('li');

    let position = 0;

    wrapper.querySelector('.prev').onclick = () => {
      position = Math.min(position + width * count, 0);
      list.style.marginLeft = `${position}px`;
      if (currentPage <= 1) {
        document.querySelector('.prev').innerHTML = 0;
        document.querySelector('.next').innerHTML = 1;
      } else {
        currentPage -= 1;
        document.querySelector('.prev').innerHTML = currentPage - 1;
        document.querySelector('.next').innerHTML = currentPage;
      }
    };

    wrapper.querySelector('.next').onclick = async () => {
      list = wrapper.querySelector('ul');
      listElems = wrapper.querySelectorAll('li');
      if (clicks === 3) {
        const modil = new AppModel(state);
        const token = await modil.getNextPageToken();
        App.renderNextPage(token);
        clicks = 0;
      }
      document.querySelector('.next').innerHTML = currentPage + 1;
      document.querySelector('.prev').innerHTML = currentPage;
      position = Math.max(position - width * count, -width * (listElems.length - count));
      list.style.marginLeft = `${position}px`;
      clicks += 1;
      currentPage += 1;
    };
  }
}
