export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  static sendRequest(url) {
    return fetch(url);
  }

  static extractClipNames(data) {
    return data.items.map(clip => clip.snippet.title);
  }

  static extractClipChannelTitle(data) {
    return data.items.map(clip => clip.snippet.channelTitle);
  }

  static extractClipImage(data) {
    return data.items.map(clip => clip.snippet.thumbnails.medium.url);
  }


  static extractClipLink(data) {
    return data.items.map(clip => clip.id.videoId);
  }

  static extractClipDate(data) {
    return data.items.map(clip => clip.snippet.publishedAt);
  }

  static extractClipDescription(data) {
    return data.items.map(clip => clip.snippet.description);
  }

  async getClipInfo() {
    const { url } = this.state;

    const responce = await AppModel.sendRequest(url);
    const data = await responce.json();
    return {
      getClipNames: AppModel.extractClipNames(data),
      getClipChannelTitle: AppModel.extractClipChannelTitle(data),
      getClipImage: AppModel.extractClipImage(data),
      getClipLink: AppModel.extractClipLink(data),
      getClipDate: AppModel.extractClipDate(data),
      getClipDescription: AppModel.extractClipDescription(data),
    };
  }

  static extractClipViewCount(data) {
    return data.items.map(clip => clip.statistics.viewCount);
  }

  async getClipVieWCount() {
    const { url } = this.state;

    const responce = await AppModel.sendRequest(url);
    const data = await responce.json();
    return AppModel.extractClipViewCount(data);
  }

  static extractNextPageToken(data) {
    return data.nextPageToken;
  }

  async getNextPageToken() {
    const { url } = this.state;

    const responce = await AppModel.sendRequest(url);
    const data = await responce.json();
    return AppModel.extractNextPageToken(data);
  }
}
